import React, { Component } from 'react'
import styled from 'styled-components'

const InputField = styled.input`
    text-align:center;
    margin-bottom:2em;
    width:30em;
`

const Flex = styled.div`
    display:flex;
    flex-direction: ${props => props.direction || "column"};
    align-items: ${props => props.alignItems || "center"};
    justify-content: ${props => props.justify || "center"};
`

const ColorBox = styled.span`
    display:flex;
    width:13px;
    height:13px;
    background-color:${props => props.color || "red"};
    border: 1px solid black;
    margin-left:5px;
    border-radius:3px`

const Option = styled.option`
    background-color:${props => props.color || "none"};
    `

export default class SouvenirForm extends Component {

    state={
        name:"",
        location:"",
        color:"",
    }

    pickColor = (e) => {
        this.setState({
            color:e.target.value
        })
    } 
  render() {
    return (
        <Flex>
            <InputField type="text" placeholder="Name"/>
            <InputField type="text" placeholder="Location"/>
            <InputField type="text" placeholder="Add song link"/>
            <Flex direction="row">
                <select onChange={this.pickColor}>
                    <option color="" value="#DC143C">Crimson</option>
                    <option value="#87CEFA">Lightblue</option>
                    <option value="#1E90FF">Blue</option>
                    <option value="#00FF7F">Green</option>
                    <option value="#000000">Black</option>
                    <option value="#FA8072">Coral</option>
                    	
                    	
                </select>
                <ColorBox color={this.state.color}/>
            </Flex>
        </Flex>
    )
  }
}
